"use client";

import Link from 'next/link';
import { useEffect, useState } from 'react';

type MeResponse = { user?: { id: number; email: string } };

export default function AuthBar() {
  const [loading, setLoading] = useState(true);
  const [email, setEmail] = useState<string | null>(null);

  useEffect(() => {
    const jwt = typeof window !== 'undefined' ? localStorage.getItem('jwt') : null;
    if (!jwt) {
      setLoading(false);
      return;
    }
    fetch('/api/auth/me', {
      headers: { Authorization: `Bearer ${jwt}` },
      cache: 'no-store',
    })
      .then(async (r) => {
        if (!r.ok) throw new Error('unauthorized');
        const d: MeResponse = await r.json();
        setEmail(d?.user?.email ?? null);
      })
      .catch(() => setEmail(null))
      .finally(() => setLoading(false));
  }, []);

  const logout = () => {
    localStorage.removeItem('jwt');
    setEmail(null);
  };

  return (
    <div className="w-full border-b bg-gray-50">
      <div className="max-w-5xl mx-auto px-4 py-2 flex items-center justify-between text-sm">
        <div className="flex items-center gap-4">
          <Link href="/">Home</Link>
        </div>
        <div>
          {loading ? (
            <span className="opacity-60">Checking authâ€¦</span>
          ) : email ? (
            <div className="flex items-center gap-3">
              <span className="opacity-80">Signed in as {email}</span>
              <button
                onClick={logout}
                className="px-2 py-1 rounded border bg-white hover:bg-gray-100"
              >
                Logout
              </button>
            </div>
          ) : (
            <div className="flex items-center gap-3">
              <Link className="px-2 py-1 rounded border bg-white hover:bg-gray-100" href="/login">Login</Link>
              <Link className="px-2 py-1 rounded border bg-white hover:bg-gray-100" href="/register">Sign up</Link>
            </div>
          )}
        </div>
      </div>
    </div>
  );
}
